.start {
    transform: scale(0);
  }
  
  .end {
    transform: scale(1);
    transition: all 0.5s;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10% 30%;
    padding: 50px 0;

    border: 1.5px solid black;
    border-radius: 20px;
  
    background-color: white;
  }
